	let consturl;
	export default {

		data() {
			return {
				// imgurl:"https://www.ccweb.xyz",
				getBulltin:"",
				index: 0,
				date: 1,
				openid:"",
				content:"",
				knowledge: [{
							question: '安装暖气片的温控阀',
							answer: '我们主要安装温控阀的目的就是可以调节热水进入暖气管道的水量，热水量多温度就高，流量少温度就低，从而来控制暖气片温度，实现室内温度的控制'
						},
						{
							
							question: '选择正确的暖气片',
							answer: '现在在暖通市场中暖气片的品牌、种类是非常多的，我们要根据实际的需求去采购暖气片，比如那些暖气片散热量大、采暖效果好并且制热效果快的才适合家用，特别死钢制暖气片，大多数选用优质冷轧低碳钢，钢材本身都有大分子的特征，水分子穿透比较好，使用的时候不会有干燥感。'
						}
					],
			}
		},
		onLoad(options) {
			let ts=this,id=uni.getStorageSync("openid");
			ts.openid=id;
			console.log(ts.openid);
			consturl=this.$consturl;
			//获取home也的公共
			// let datas=ts.$ajax("wx/api/getBulltin","home公告");
			// console.log(consturl);
			// setTimeout(function(){
			// 	ts.getBulltin=datas;
			// 	console.log(ts.getBulltin)
			// },1000);
			// console.log(ts.index);
			// 获取住户home的公告
			uni.request({
				url:consturl+"wx/api/getBulltin", //
				data: {
					type:"user"
				},
				method: 'GET',
				success: function (res) {
					ts.getBulltin=	res.data.data;
					console.log(ts.getBulltin);
					
				},
				fail:function(){
					console.log("住户home的公告获取失败");
				}
			});
			// 获取home页下方的知识库
			uni.request({
				url:"http://171.84.0.89:5080/page/CustomerService/WeChatHandler.ashx", //
				data: {
					action:"questionInfo"
				},
				method: 'GET',
				success: function (res) {
					console.log("知识获取失败success");
					let r= res.data.data;
					if(r!=""&&r!=null&&r!=undefined){
						ts.knowledge=r;
						console.log(res.data.data);
					}	
				},
				fail:function(){
					console.log("知识获取失败失败");
				}
			});
		},
		onShow:function(){
			// console.log("home_show");
		},
		methods: {
             //判断是否登录事件
			 // 判断登录后点击了哪一模块
			click: function(e) {
				let that=this;
				let c = e.currentTarget.id,opid=that.openid;
				if(opid!=""&&opid!=null&&opid!=undefined) {
					console.log(opid!=""&&opid!=null&&opid!=undefined);
					// uni.reLaunch({
					//     url: '../userRepair/repairList'
					// });
					if (c == "baoxiu1") {
						uni.navigateTo({
							url: '../userRepair/repairsList'
						});
					}
					if (c == "baoxiu2") {
						uni.navigateTo({
							url: '../userRepair/repairList'
						});
					}
					if (c == "baoxiu3") {
						uni.navigateTo({
							// url: '../userRepair/Recorder',
							url:"../userRepair/securityList"
						});
					}
					if (c == "fuwu1") {
						uni.navigateTo({
							url: '../pay/checkPaymentDelail'
						});
					}
					if (c == "fuwu2") {
						uni.navigateTo({
							url: '../bill/bill'
						});
					}
					if (c == "fuwu3") {
						uni.navigateTo({
							url: '../proposal/proposal'
						});
					}
					if (c == "fuwu4") {
						uni.showModal({
						    title: '提示',
						    content: '是否注销账号',
						    success: function (res) {
						        if (res.confirm) {
						            uni.setStorageSync("openid","");
									uni.setStorageSync("adress","");
									uni.navigateTo({
										url:"../index/index",
										// url:"../company/pHome"
									});
						        } else if (res.cancel) {
						            console.log('用户点击取消');
						        }
						    }
						});
					}
					if (c == "fuwu5") {
						uni.navigateTo({
							url: 'pdf?type=5'
						});
					}
					if (c == "fuwu6") {
						uni.navigateTo({
							url: 'pdf'
						});
					}
				} else {
					uni.showModal({
						title: '提示',
						content: '您未登录，是否选择登录',
						success: function(res) {
							if (res.confirm) {
								uni.navigateTo({
									url: '../index/index'
								});
							} else if (res.cancel) {
								console.log('用户点击取消');
							}
						}
					});
				}
			},
			
		}
	}
